import { Router, ActivatedRoute } from '@angular/router';
import { MainService } from '../../../services/main.service';

import { Component, OnInit } from '@angular/core';
import { error } from '@angular/compiler/src/util';

@Component({
    selector: 'app-verify-account',
    templateUrl: './verify-account.component.html',
    styleUrls: ['./verify-account.component.scss']
})
export class VerifyAccountComponent implements OnInit {

    public valid = null;


    constructor(
        public main: MainService,
        public router: Router,
        private route: ActivatedRoute,
    ) {

    }

    async ngOnInit() {

        if(this.main.currentUser?.email == null){
            this.router.navigate(['/login']);
        }
        
    }

    ngOnDestroy() { this.main.onDestroyPage(); }

    countDownTimer(){}

    reSendEmail(){
        this.main.provider.reenviarCorreuActivacio(this.main.currentUser.email);
    }

    checkAcces(){
        let response: any;
        //TODO : Necessaria una peticio per poder compravar si el email del usuari que es situa dins de currentUser ja esta donat de alta o com a minim si ha fet el intent de registrar-se
        this.main.provider.checkEmail(this.main.currentUser.email).then(
            (res) => {
                response = res;
            }).catch((err) => {
                console.log(err); //Error code
            });
        return response;
    }

}
